	module.exports = {"data":{"challengeNode":{"title":"Implement Selection Sort","description":["现在我们开始实现<a href='https://baike.baidu.com/item/%E9%80%89%E6%8B%A9%E6%8E%92%E5%BA%8F' target='_blank'>选择排序</a>。选择排序是通过选择列表中最小值来与列表中的第一个值进行对比交换，然后从第二位置开始逐一对比，选择剩下的列表中最小值与第二个元素交换位置。然后循环遍历列表并交换元素，直到列表最后一个元素，此时的列表就完成了排序。选择排序在所有的情况下都具有二次<a href='https://baike.baidu.com/item/%E6%97%B6%E9%97%B4%E5%A4%8D%E6%9D%82%E5%BA%A6' target='_blank'>时间复杂度</a>。","<strong>说明：</strong>创建一个函数并命名为<code>selectionSort</code>，输入参数是一个数组，且数组元素全部都是整数类型，然后按照从最小到最大的顺序返回整个数组。","<strong>注意：</strong><br>我们将在后台调用这些函数，测试数组在编辑器中也是被注释掉了的。尝试记录输入<code>array</code>来确认你的冒泡排序算法是否正确。","如果你遇到了困难，请点击 <a href='https://forum.freecodecamp.one/t/topic/157' target='_blank'> 帮助 </a>。你可以找人 “结对编程”，但不要因此放弃思考。"],"challengeType":1,"fields":{"slug":"/coding-interview-prep/algorithms/implement-selection-sort","blockName":"Algorithms","tests":[{"text":"<code>selectionSort</code>是一个函数。","testString":"assert(typeof selectionSort == 'function', '<code>selectionSort</code>是一个函数。');"},{"text":"<code>selectionSort</code>返回的应该是按照从最小到最大的排序的数组。","testString":"assert(isSorted(selectionSort([1,4,2,8,345,123,43,32,5643,63,123,43,2,55,1,234,92])), '<code>selectionSort</code>返回的应该是按照从最小到最大的排序的数组。');"},{"text":"<code>selectionSort</code>返回的数组除了顺序之后应该是没有任何其他的改变的。","testString":"assert.sameMembers(selectionSort([1,4,2,8,345,123,43,32,5643,63,123,43,2,55,1,234,92]), [1,4,2,8,345,123,43,32,5643,63,123,43,2,55,1,234,92], '<code>selectionSort</code>返回的数组除了顺序之后应该是没有任何其他的改变的。');"},{"text":"<code>selectionSort</code>不要使用内置的排序方式：<code>.sort()</code>。","testString":"assert.strictEqual(code.search(/\\.sort\\(/), -1, '<code>selectionSort</code>不要使用内置的排序方式：<code>.sort()</code>。');"}]},"required":[],"files":{"indexhtml":null,"indexjs":{"key":"indexjs","ext":"js","name":"index","contents":"function selectionSort(array) {\n  // 请在下方区域编写代码\n\n  // 请在上方区域编写代码\n  return array;\n}\n\n// 测试数组：\n// [1, 4, 2, 8, 345, 123, 43, 32, 5643, 63, 123, 43, 2, 55, 1, 234, 92]","head":"","tail":"function isSorted(arr) {\n  var check = (i) => (i == arr.length - 1) ? true : (arr[i] > arr[i + 1]) ? false : check(i + 1);\n  return check(0);\n};"},"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/algorithms/implement-insertion-sort","id":"587d8259367417b2b2512c85"},"slug":"/coding-interview-prep/algorithms/implement-selection-sort"}}

